@import '../../../style/variables.scss';

.page404 {
  width:1100px;
  min-height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
  position: relative;
  left:6vmin;
  text-align: center;
  .cog-wheel1, .cog-wheel2{
    transform:scale(0.7);
  }
  
  .cog1, .cog2 {
    width:40vmin;
    height:40vmin;
    border-radius:50%;
    border:6vmin solid #f3c623;
    position: relative;
  }
  
  
  .cog2{
    border:6vmin solid #4f8a8b;
  }
  
  .top, .down, .left, .right, .left-top, .left-down, .right-top, .right-down{
    width:10vmin;
    height:10vmin;
    background-color: #f3c623;
    position: absolute;
  }
  
  .cog2 .top,.cog2  .down,.cog2  .left,.cog2  .right,.cog2  .left-top,.cog2  .left-down,.cog2  .right-top,.cog2  .right-down{
    background-color: #4f8a8b;
  }
  
  .top{
    top:-14vmin;
    left:9vmin;
  }
  
  .down{
    bottom:-14vmin;
    left:9vmin;
  }
  
  .left{
    left:-14vmin;
    top:9vmin;
  }
  
  .right{
    right:-14vmin;
    top:9vmin;
  }
  
  .left-top{
    transform:rotateZ(-45deg);
    left:-8vmin;
    top:-8vmin;
  }
  
  .left-down{
    transform:rotateZ(45deg);
    left:-8vmin;
    top:25vmin;
  }
  
  .right-top{
    transform:rotateZ(45deg);
    right:-8vmin;
    top:-8vmin;
  }
  
  .right-down{
    transform:rotateZ(-45deg);
    right:-8vmin;
    top:25vmin;
  }
  
  .cog2{
    position: relative;
    left:-10.2vmin;
    bottom:10vmin;
  }
  
  h1{
    color:#142833;
  }
  
  .first-four{
    position: relative;
    left:6vmin;
    font-size:40vmin;
  }
  
  .second-four{
    position: relative;
    right:18vmin;
    z-index: -1;
    font-size:40vmin;
  }
  
  .wrong-para{
    position: absolute;
    bottom:10vmin;
    padding:3vmin 12vmin 3vmin 3vmin;
    font-weight:600;
    font-size: 30px;
    color:#092532;
  }
  .wrong-para p {
    text-decoration: underline;
    color: $main-color;
  }

}

